    text-align: justify;
    color: rgba(255, 255, 255, 0.795);
  }
  .AboutMe__skillset {
    margin-top: 5rem;
    margin-left: 0rem;
  }
  .AboutMe__skillset__charts {
    flex-direction: column;
    align-items: center;
  }
  .AboutMe__skillset__charts__panel {
    max-width: 50%;
  }
  #AboutMe__chart {
