  }
  #AboutMe__carouselItem__image {
    transform: scale(0.5);
    top: -12rem;
  }
  #AboutMe__carouselItem__name {
    margin-top: -17rem;
    font-size: 1.5rem;
  }
  #AboutMe__carouselItem__title {
    font-size: 1.2rem;
  }
