  /* color: #032174; */
}
.Footer__navigation {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Footer__navigation__button {
  display: flex;
  flex-direction: row;
  align-items: center;
  border: none;
  background-color: none;
  border-radius: 40px;
  padding: 0px 20px;
  background-color: #000056;
  color: #012997;
}
.Footer__navigation__button:hover {
  cursor: pointer;
  color: #1e42a3;
}

.Footer__navigation__button:hover > #Footer__upIcon {
  animation-name: up;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}
@keyframes up {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0px);
  }
}
